  methods: {
    insertData() {
      const param = {
        id: this.id,
        title: this.title,
        writer: this.writer,
      }
      // eslint-disable-next-line no-undef
      axios.post(`${this.$window.API_URL}notices`, param)
        .then(res => {
          if (res.status === 200 || res.status === 201) {
          // eslint-disable-next-line no-alert
            alert('success')
            this.title = ''
            this.id = ''
            this.writer = ''
          } else {
          // eslint-disable-next-line no-alert
            alert('fail')
          }
        }).catch(() => {
          // eslint-disable-next-line no-alert
          alert('catch error')
        })
    },
